let searchInput:HTMLInputElement;
let searchResults:HTMLDivElement;

function onInput () {
    if (this.classList.contains('active')) {
        searchResults.style.width = this.offsetWidth + "px";
        searchResults.classList.remove('hide');

        // сделать так что бы иконка отображалась лишь когда идёт загрузка
        this.parentElement.classList.add('loading');

        searchResults.style.marginLeft = searchInput.offsetLeft + "px";
    } else {
        searchResults.classList.add('hide');
        this.parentElement.classList.remove('loading');
    }
}

export default function () {

    const searchContainer:HTMLElement = document.querySelector('#global-search') as HTMLElement;
    searchInput = searchContainer.querySelector('#global-search-input') as HTMLInputElement;
    searchResults = searchContainer.querySelector('.search-results') as HTMLDivElement;

    searchInput.removeEventListener('input', onInput);
    searchInput.addEventListener('input', onInput);

    searchResults.classList.add('hide');

}